import React, { useState } from 'react'
import { useWindowScroll } from 'beautiful-react-hooks'
import { PanelHeader, Div } from '@vkontakte/vkui'

import './MainEpicHeader.css'

const MainEpicHeader = ({ children, ...props }) => {
  const [showPanelHeader, setShowPanelHeader] = useState(false)
  useWindowScroll(() => {
    const headerHeight = 50
    setShowPanelHeader(window.scrollY >= headerHeight)
  })

  return (
    <>
      <PanelHeader {...props} separator={false} transparent={!showPanelHeader}>{showPanelHeader && children}</PanelHeader>
      <Div className="MainEpicHeader__text">{children}</Div>
    </>
  )
}

export default MainEpicHeader
