import React from 'react';
import bridge from '@vkontakte/vk-bridge';
import { View, Epic, Tabbar, TabbarItem, Panel, Gallery, Title, Div, Button } from '@vkontakte/vkui';
import Icon28HomeOutline from '@vkontakte/icons/dist/28/home_outline';
import Icon28More from '@vkontakte/icons/dist/28/more';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Profile from './panels/Profile';
import './styles.css';

import ava from './img/ava.png'
import intro1 from './img/AppleMusic.svg'
import intro2 from './img/AppleMusic.svg'

class App extends React.Component {
  constructor (props) {
    super(props);

    this.state = {
      activeStory: 'home',
	  popout: null,
	  tabbar: true
    };
	  this.onStoryChange = this.onStoryChange.bind(this);
  }

  componentDidMount () {
	bridge.subscribe(({ detail: { type, data }}) => {
      if (type === 'VKWebAppViewHide') {
        this.setState({popout: null})
      }
	});
	bridge.send("VKWebAppStorageGet", {"keys": ["greeting"]})
		.then((haveshown)=>{
      console.log('haveshown', haveshown)
			//bridge.send("VKWebAppStorageSet", {"key": "greeting", "value": ''})
			if (haveshown?.keys[0]?.value === '') {
				this.setState({activeStory: 'greeting', tabbar: null})
				document.documentElement.style.setProperty('--padding', '0');
			}
    })
    .catch((e)=>console.log('e', e));
  };

  onStoryChange (e) {
	this.setState({ activeStory: e.currentTarget.dataset.story })
	bridge.send("VKWebAppTapticSelectionChanged", {});
  }

  render () {
    return (
      <Epic activeStory={this.state.activeStory} tabbar={
		this.state.tabbar ?
        <Tabbar>
          <TabbarItem
            style={{cursor: 'pointer'}}
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'home'}
			data-story="home"
			text={this.state.activeStory === 'home' ? 'Главная' : null}
          ><Icon28HomeOutline /></TabbarItem>
          <TabbarItem
            style={{cursor: 'pointer'}}
            onClick={this.onStoryChange}
            selected={this.state.activeStory === 'profile'}
			data-story="profile"
			text={this.state.activeStory === 'profile' ? 'Разное' : null}
          ><Icon28More /></TabbarItem>
        </Tabbar>
		: null
      }>
        <View id="home" activePanel="home" popout={this.state.popout}>
			<Home setPopout={popout => this.setState({ popout })} setTabbar={tabbar => this.setState({ tabbar })} id='home' />
        </View>
        <View id="profile" activePanel="profile">
			<Profile id='profile' />
        </View>

      <View id="greeting" activePanel="greeting">
        <Panel id="greeting" style={{ position: 'fixed' }}>
		  <Gallery 
		  		slideWidth="100%"
                align="center"
                style={{ height: '100vh' }}
                slideIndex={this.state.slideIndex}
				onChange={slideIndex => this.setState({slideIndex})}
				bullets="light"
		  >
		  	<div style={{ backgroundColor: '#8818d1', display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column' }}>
			  	<img className='IntroImg' src={ava} alt="" style={{ width: '300px', height: '300px', marginBottom: 64 }} />
				<Title level="1" weight="heavy" style={{ color: '#fff', marginBottom: 16, fontSize: '2.5em', textAlign: 'center' }}>Конвертер</Title>
				<Title level="3" weight="semibold" style={{ color: '#fff', marginBottom: 4, textAlign: 'center' }}>
					Добро пожаловать в Конвертер!
				</Title>
				<Title level="5" weight="semibold" style={{ color: '#fff', textAlign: 'center', marginBottom: 48, padding: '0 10px' }}>
					Свайпни влево, чтобы узнать подробнее.
				</Title>
				<Div>
				  <Button style={{cursor: 'pointer'}} size='xl' mode='commerce' onClick={()=> {
					  bridge.send("VKWebAppStorageSet", {"key": "greeting", "value": 'true'})
					  this.setState({ activeStory: 'home', tabbar: true })
					  document.documentElement.style.setProperty('--padding', 'calc(var(--safe-area-inset-bottom) + var(--tabbar_height))');
					}}>
					Пропустить
				  </Button>
				</Div>
			</div>
			<div style={{ backgroundColor: '#a3c6c7', display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column' }}>
			  	<img className='IntroImg' src={intro1} alt="" style={{ width: '300px', height: '300px', marginBottom: 64 }} />
				<Title level="1" weight="heavy" style={{ color: '#fff', marginBottom: 16, fontSize: '2.5em', textAlign: 'center' }}>
					Форматы
				</Title>
				<Title level="3" weight="semibold" style={{ color: '#fff', marginBottom: 48, textAlign: 'center', padding: '0 10px' }}>
					Доступны самые популярные расширения, такие как PNG, JPG, SVG и так далее...
				</Title>
				<Div>
				  <Button style={{cursor: 'pointer'}} size='xl' mode='commerce' onClick={()=>this.setState({slideIndex: 2})}>
					Вау
				  </Button>
				</Div>
			</div>
			<div style={{ backgroundColor: '#fe96ce', display: 'flex', justifyContent: 'center', alignItems: 'center', flexDirection: 'column' }}>
			  	<img className='IntroImg' src={intro2} alt="" style={{ width: '300px', height: '300px', marginBottom: 64 }} />
				<Title level="1" weight="heavy" style={{ color: '#fff', marginBottom: 16, fontSize: '2.5em', textAlign: 'center' }}>
					Безопасность
				</Title>
				<Title level="3" weight="semibold" style={{ color: '#fff', marginBottom: 48, textAlign: 'center', padding: '0 10px' }}>
					Всё происходит в реальном времени и без сохранения на сторонние ресурсы.
				</Title>
				<Div>
				  <Button style={{cursor: 'pointer'}} size='xl' mode='commerce' onClick={()=> {
					  bridge.send("VKWebAppStorageSet", {"key": "greeting", "value": 'true'})
					  this.setState({ activeStory: 'home', tabbar: true })
					  document.documentElement.style.setProperty('--padding', 'calc(var(--safe-area-inset-bottom) + var(--tabbar_height))');
					}}>
					Перейти в сервис!
				  </Button>
				</Div>
			</div>
		  </Gallery>
     </Panel>
     </View>
      </Epic>
    )
  }
}

export default App;