import React from 'react'
import bridge from '@vkontakte/vk-bridge'
import axios from 'axios-jsonp-pro'
import PanelHeader from '../components/MainEpicHeader'
import { Header, List, Cell, Spinner, Button, Switch, Panel, Group, View, Caption, Avatar, Snackbar } from '@vkontakte/vkui'
import Icon24Share from '@vkontakte/icons/dist/24/share'
import Icon16Done from '@vkontakte/icons/dist/16/done'
import Icon16Cancel from '@vkontakte/icons/dist/16/cancel'
import Icon16Like from '@vkontakte/icons/dist/16/like'

import ava from '../img/ava.png'

import packagejson from '../../package.json'

export default class Profile extends React.Component {
  constructor (props) {
    super(props)

    this.state = {
	  activePanel: 'profile',
	  vk_platform: '',
	  files_count: <Spinner size="small" />,
	  files_size: <Spinner size="small" />,
	  subscriber: false,
	  notifications: false,
    snackbar: null
    }
  }

  async componentDidMount () {
    if (window.location.search) {
      var a = window.location.search.match(/vk_platform=([a-z_]+)/)
      var notifications = await bridge.send('VKWebAppStorageGet', { keys: ['notifications'] })
      if (!(['true', 'false'].includes(notifications.keys[0].value))) notifications.keys[0].value = 'false'
      this.setState({ vk_platform: a[1].toString() })
      var user = window.location.search.match(/vk_user_id=([1,2,3,4,5,6,7,8,9,0]+)/)
      user = user[1].toString()
	 	const url = 'https://converter.dan0102dan.ru/vk'
      const formData = new FormData()
      var sign, response
      if (this.state.vk_platform !== 'browser') {
        sign = window.location.search.slice(1)
      } else sign = 'null'
      formData.append('sign', sign)
      formData.append('id', user)
      const mode = 'cors'
      await axios.post(url, formData, mode)
        .then(async (res) => {
          response = res
        })
      if (!response.data.sign) {
        console.log(response.data)
        if (response.data.messages) this.setState({ notifications: JSON.parse(notifications.keys[0].value) });
        else this.setState({ notifications: response.data.messages });
        this.setState({ subscriber: response.data.subscriber })
      } else {
        console.log(response.data.sign)
        return
      }

      var key = await bridge.send('VKWebAppStorageGet', { keys: ['transfers', 'size'] }).catch(()=>this.setState({files_count: 'Нет данных', files_size: 'Нет данных'}))
//количество файлов
    try {
      if (key.keys[0].value === '') {
        this.setState({ files_count: 'Нет данных' })
      }
      else if (!isNaN(key.keys[0].value)) {
        this.setState({ files_count: key.keys[0].value })
      }
    } catch {
      this.setState({ files_size: 'Нет данных' })
    }
//размер файлов
    try {
      if (key.keys[1].value === '') {
        this.setState({ files_size: 'Нет данных' })
      }
      else if (!isNaN(key.keys[1].value)) {
        this.setState({ files_size: (Number(key.keys[1].value) / 1000000).toFixed(3) + ' МБ' })
      }
    } catch {
        this.setState({ files_size: 'Нет данных' })
      }
    }
  }

  openSuccess () {
    this.setState({
      snackbar:
      <Snackbar
        layout="vertical"
        onClose={() => this.setState({ snackbar: null })}
        before={<Avatar size={24} style={{ backgroundColor: 'var(--accent)' }}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}
      >
        Уведомления включены!
      </Snackbar>
    })
  }

  openError () {
    this.setState({
      snackbar:
      <Snackbar
        layout="vertical"
        onClose={() => this.setState({ snackbar: null })}
        before={<Avatar size={24} style={{ backgroundImage: 'linear-gradient(135deg, #fc7373, #cb5e5a)' }}><Icon16Cancel fill="#fff" width={14} height={14} /></Avatar>}
      >
        Уведомления отключены!
      </Snackbar>
    })
  }

  openThanks () {
    this.setState({
      snackbar:
      <Snackbar
        layout="vertical"
        onClose={() => this.setState({ snackbar: null })}
        before={<Avatar size={24} style={{ backgroundImage: 'linear-gradient(135deg, #ffb73d, #ffa000)' }}><Icon16Like fill="#fff" width={14} height={14} /></Avatar>}
      >
        Спасибо за подписку!!
      </Snackbar>
    })
  }

  render () {
    var active = false;
    return (
  	<Panel id={this.id} snackbar={this.state.snackbar}>
	 <View activePanel={this.state.activePanel}>
	 	<Panel id="profile">
            <PanelHeader>Профиль</PanelHeader>
            <Group>
			 <img src={ava} alt="" style={{ width: '100px', height: '100px', display: 'block', marginLeft: 'auto', marginRight: 'auto', backgroundColor: '#202020', borderRadius: 25 }} />
			 <Caption level="1" weight="regular" style={{ marginTop: 7, marginBottom: 16, textAlign: 'center' }}>Версия: {packagejson.version}</Caption>
            </Group>

    <Group header={<Header mode="secondary">Статистика</Header>}>
		 <List>
                <Cell indicator={this.state.files_count}>Всего конвертировано</Cell>
                <Cell indicator={this.state.files_size}>Размер всех файлов</Cell>
              </List>
	 	</Group>

		 <Group>
		  <Cell asideContent={<Switch checked={ this.state.notifications === true } onChange={async (e) => {
        if (active === true) return;
        else active = true;
			  if (e.target.checked) {
                  var mes = await bridge.send('VKWebAppAllowMessagesFromGroup', { group_id: 151064772 }).catch(() => {
                    this.openError()
                    this.setState({ notifications: false })
                  })
                  if (mes?.result) {
                    this.setState({ notifications: true })
                    this.openSuccess()
                    await bridge.send('VKWebAppStorageSet', { key: 'notifications', value: 'true' })
                  } else {
                    this.openError()
                    this.setState({ notifications: false })
                    await bridge.send('VKWebAppStorageSet', { key: 'notifications', value: 'false' })
                  }
                  active = false;
			  } else {
                  this.openError()
                  this.setState({ notifications: false })
                  active = false;
                  await bridge.send('VKWebAppStorageSet', { key: 'notifications', value: 'false' })
			  }
		  }} />
		  }>
          	Уведомления от бота
              </Cell>
      </Group>

    <Group>
		  <Cell description={<a style={{color: '#30ab65', textDecoration: 'none'}} rel="noopener noreferrer" href="https://vk.com/public151064772" target="_blank">Конвертер</a>} indicator={this.state.subscriber ? <Button mode="secondary" disabled>Подписчик</Button> : <Button style={{cursor: 'pointer'}} onClick={ async () => { await bridge.send('VKWebAppJoinGroup', { group_id: 151064772 }); this.setState({ subscriber: true }); this.openThanks() }}>Вступить</Button>}>Сообщество</Cell>
	 	</Group>

		<Group>
		 <Button style={{cursor: 'pointer'}} mode="tertiary" size='xl' before={<Icon24Share />} onClick={ () => bridge.send('VKWebAppShare', { link: 'https://vk.com/app7561776' }) }>
            Поделиться сервисом
     </Button>
	 	</Group>
		 {this.state.snackbar}
          </Panel>
        </View>
      </Panel>
    )
  }
}
