import React from 'react';
import bridge from '@vkontakte/vk-bridge';
import Papa from 'papaparse';

//import { animated, useSpring } from 'react-spring'

import { FixedSizeList as List } from "react-window";

import io from 'socket.io-client';
//import axios from 'axios-jsonp-pro'
import Header from '../components/MainEpicHeader'
import Banner from '../components/Banner'
import { PanelHeader, View, Panel, Snackbar, Avatar, ConfigProvider, SimpleCell, ScreenSpinner } from '@vkontakte/vkui';
import Icon16Done from '@vkontakte/icons/dist/16/done';
import Icon16Cancel from '@vkontakte/icons/dist/16/cancel';

const images = { AppleMusic: require('../img/AppleMusic.svg'), Spotify: require('../img/Spotify.svg'), Yandex: require('../img/Yandex.svg') }

export default class Home extends React.Component {
	constructor(props) {
		super(props);
	
		this.state = {
		  activePanel: 'home',
		  history: ['home'],
		  snackbar: null,
		  vk_platform: 'browser',
		  songs: [],
		  count: 0
		}
	  }

	  goBack = () => {
		var history = this.state.history;

		if (this.state.activePanel === 'home') {
			bridge.send('VKWebAppDisableSwipeBack');
		}
		
		if ( history.length > 1 ) {
		  history.pop()
		  if (history[history.length - 1] === 'home') {
			this.props.setTabbar(true)
			document.documentElement.style.setProperty('--padding', 'calc(var(--safe-area-inset-bottom) + var(--tabbar_height))');
			document.documentElement.style.setProperty('--bottom', 'calc(var(--tabbar_height) + var(--safe-area-inset-bottom))');
		  }
		  this.setState({ activePanel: history[history.length - 1] })
		}
	  }
	  goToPage( name ) {
		window.history.pushState( { panel: name }, name );
		if (this.state.activePanel === 'home') {
			bridge.send('VKWebAppEnableSwipeBack');
			this.props.setTabbar(null)
			document.documentElement.style.setProperty('--padding', '0');
			document.documentElement.style.setProperty('--bottom', 'var(--safe-area-inset-bottom)');
		}
		this.setState({ 
			activePanel: name,
			history: [...this.state.history, name]
		})
	  }

	  openSuccess () {
		this.setState({ snackbar:
		  <Snackbar
			layout="vertical"
			onClose={() => this.setState({ snackbar: null })}
			before={<Avatar size={24} style={{ backgroundColor: 'var(--accent)' }}><Icon16Done fill="#fff" width={14} height={14} /></Avatar>}
		  >
			Отправили!
		  </Snackbar>
		});
	  }
	  openError () {
		this.setState({ snackbar:
		  <Snackbar
			layout="vertical"
			onClose={() => this.setState({ snackbar: null })}
			before={<Avatar size={24} style={{ backgroundImage: 'linear-gradient(135deg, #fc7373, #cb5e5a)' }}><Icon16Cancel fill="#fff" width={14} height={14} /></Avatar>}
		  >
			Ошибка
		  </Snackbar>
		});
	  }

	  componentDidMount() {
		window.addEventListener('popstate', () => this.goBack());
		if (window.location.search) {
			var vk_platform = window.location.search.match(/vk_platform=([a-z_]+)/)
			this.setState({vk_platform: vk_platform[1].toString()})
		}
	  }

render () {
	return (
	<Panel id={this.id}>
	 <ConfigProvider
	 scheme={document.body.getAttribute('scheme') ? document.body.getAttribute('scheme') : 'bright_light'}
	 >
	  <View activePanel={this.state.activePanel} history={this.state.history} onSwipeBack={this.goBack}>
	 	<Panel id="home">
          <Header>Главная</Header>
		  <Banner
		  	id='Apple'
		  	mode="image"
		  	background={
				<div
				  id='BannerBackground'
				  style={{
					backgroundImage: 'linear-gradient(326deg, #E99BA6 0%, #FFD9DE 74%)'
				  }}
				/>
			  }
			before={<Avatar style={{background: 'none'}} size={86} mode="image" src={images.AppleMusic} />}
            header="Apple Music"
			subheader="Перенести музыку из файла Apple."
			onClick={() => document.getElementById('AppleInput').click()}
          >
			<input
      		  style={{display: 'none'}}
			  type="file"
			  accept='.txt'	
      		  id="AppleInput"
      		  onChange={(e)=>{
        		console.log(e.target.files[0])
				this.props.setPopout(<ScreenSpinner />);
				let reader = new FileReader();
				var text, playlist = [];

				reader.readAsText(e.target.files[0], 'UTF-8');
				reader.onload = (evt) => {
					text = evt.target.result;

					let parsed = Papa.parse(text, {
						header: true
					  });
					if (parsed.errors.length && !parsed.data.length) {
						this.openError()
					}
					parsed.data.pop();
					for (let index = 0; parsed.data.length > index; index++) {
						const
						  track = parsed.data[index],
						  keys = Object.keys(track);
						if (!keys.includes('Name') || !keys.includes('Artist') || !keys.includes('Album')) {
							this.openError()
						}
						playlist.push({
						  index,
						  name           : track.Name,
						  artist         : track.Artist,
						  album          : track.Album
						});
					  }
				this.setState({count: playlist.length}, () => this.goToPage('transfer'));

				const socket = io('https://music.dan0102dan.ru');
				this.props.setPopout(null);
				socket.on('connect', () => {
					socket.emit('AppleMusic', playlist);
					socket.on('message', (data) => {
						var songs = this.state.songs;
						songs.unshift(data);
						this.setState({songs});
					});
				  });
			  }
	  		  }}
	   		/>
		  </Banner>
		  <Banner
		  	id='Spotify'
		  	mode="image"
		  	background={
				<div
				  id='BannerBackground'
				  style={{
					backgroundImage: 'linear-gradient(326deg, #000000 0%, #313131 74%)'
				  }}
				/>
			  }
            before={<Avatar style={{background: 'none'}} size={86} mode="image" src={images.Spotify} />}
            header="Spotify"
			subheader="Перенести музыку из плейлиста Spotify."
          />
		  <Banner
		  	id='Yandex'
		  	mode="image"
		  	background={
				<div
				  id='BannerBackground'
				  style={{
					backgroundImage: 'linear-gradient(326deg, #F9D976 0%, #fdf7ec 74%)'
				  }}
				/>
			  }
			before={<Avatar style={{background: 'none'}} size={86} mode="image" src={images.Yandex} />}
            header="Yandex Music"
			subheader="Перенести музыку из плейлиста Yandex."
          />
	  	{this.state.snackbar}
      </Panel>

	  <Panel id="transfer">
          <PanelHeader separator={false}>Найдено {this.state.songs.length}/{this.state.count}</PanelHeader>
      		<List
        	  className="List"
			  height={window.innerHeight-56}
			  width={window.innerWidth}
			  itemCount={this.state.songs.length}
			  itemSize={60}
      		>
        	 {({ index, style }) => (
			<div style={style}>
			  <SimpleCell
			 	key={this.state.songs[index].id}
			 	before={<Avatar mode="image" src={this.state.songs[index].image} />}
			 	description={this.state.songs[index].artist}
				after={this.state.songs[index].duration}>
			  	{this.state.songs[index].title}
			  </SimpleCell>
			</div>
	  		)}
      		</List>
	  	{this.state.snackbar}
      </Panel>
      </View>
	 </ConfigProvider>
	</Panel>
	)
 }
}